html {
font-family: Arial, Helvetica, sans-serif;
font-weight: 700;
font-size: 20px;
color: #2F4F4F;
}

body {
margin: 0;
}

.people {
  display: grid;
  justify-content: center;
  grid-template-columns: repeat(2, 200px);
  gap: 20px 10px;
  padding-top: 30px;
  
  @media (min-width: 1000px) {
    grid-template-columns: repeat(4, 200px);
  }
  
  &__person {
    position: relative;
    padding: 10px;
    background-color: white;
    border-radius: 10px;
    animation: scaleUp 1s, borderRad 1s;
    animation-direction: alternate;

    &:hover {
      background-color: #EEEEEE;
      @keyframes scaleUp {
        0% {
          transform: scale(1);
        }
        100% {
          transform: scale(1.2);
        }
      }
    }

    &:hover &__img {
      @keyframes borderRad {
        0% {
          border-radius: 50%;
        }
        100% {
          border-radius: 20%;
        }
      };
    }

    &__photo {
      position: relative;
      display: flex;
      margin-bottom: 8px;
    }
  
    &__img {
      width: 100%;
      border-radius: 50%;
    }
  
    &__rank {
      position: absolute;
      display: flex;
      justify-content: center;
      align-items: center;
      top: 84px;
      right: 10px;
      background-color: #FFF;
      border-radius: 50%;
      color: #8B0000;
      height: 32px;
      width: 32px;
    }
  
    &__details {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  
    &__name {
      margin-bottom: 9px;
    }
  
    &__sport {
      font-weight: 400;
      font-size: 18px;
      color: #000;
    }
  }
}
